html {
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  font-family: "ABeeZee", Georgia, 'Arial';
  background: #5D26C1;
  background-attachment: fixed;
  min-height: 100vh;
  padding: 8px;
  display: grid;
  grid-template-columns: 1fr 10fr 1fr; }

.main {
  min-width: 480px;
  max-width: 928px;
  width: 100%;
  height: fit-content;
  margin: auto;
  padding: 40px 10px;
  box-shadow: 0 0 12px rgba(0, 0, 0, 0.247);
  background: #ffffff87;
  grid-column-start: 2;
  display: grid;
  grid-template-areas: ". header ." ". note ." ". form ." ". buttons .";
  grid-row-gap: 24px; }

.header {
  margin: 0;
  font-family: 'Cabin Sketch', Georgia, 'Arial';
  color: #fff;
  text-align: center;
  grid-area: header;
  font-size: 64px; }

.form {
  grid-area: form;
  margin: 0; }
  .form__questions {
    width: fit-content;
    padding: 10px;
    margin: 0;
    font-size: 12px;
    border-radius: 4px;
    list-style: none;
    background: lemonchiffon;
    position: absolute;
    align-self: auto;
    z-index: 2;
    opacity: 0;
    transition: opacity 0.5s linear; }
    .form__questions--show {
      opacity: 1; }

.note {
  max-width: 736px;
  width: 100%;
  margin: 0;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  color: #fff;
  grid-area: note; }

.textarea {
  width: 100%;
  min-height: 312px;
  height: 100%;
  padding: 10px;
  resize: vertical;
  position: relative;
  border-radius: 4px; }

.textarea-container {
  width: 100%;
  margin: 0; }

.button {
  display: flex;
  justify-content: flex-end;
  grid-area: buttons; }
  .button__load {
    max-width: 200px;
    width: 100%;
    height: 48px;
    border-radius: 4px;
    border: none;
    color: #fff;
    margin-right: 14px;
    background: linear-gradient(180deg, #4DBC7C 0%, #219653 100%); }
    .button__load:hover {
      background: #219653; }
  .button__save {
    max-width: 200px;
    width: 100%;
    height: 48px;
    border-radius: 4px;
    border: none;
    color: #fff;
    background: linear-gradient(180deg, #DC6262 0%, #C83939 100%);
    position: relative; }
    .button__save:hover {
      background: #C83939; }

.prompt {
  max-width: 200px;
  width: fit-content;
  padding: 8px;
  font-size: 12px;
  background: lemonchiffon;
  color: #000;
  position: absolute;
  margin-top: 50px;
  border-radius: 4px;
  opacity: 1;
  transition: opacity .5s linear; }
  .prompt--hidden {
    opacity: 0; }

@media (max-width: 640px) {
  .header {
    font-size: 32px; }
  .main {
    grid-row-gap: 14px; } }

@media (max-width: 500px) {
  body {
    padding: 2px;
    display: block;
    font-size: 12px; }
  .main {
    min-width: 300px;
    max-width: 500px;
    width: 100%; }
  .note {
    font-size: 12px; }
  .prompt {
    display: none; }
  .textarea__questions {
    max-width: 280px;
    width: 100%;
    font-size: 10px; } }
